$color0 : #111;
$color1 : #666;
$color2 : #999;
$yellow : #ff9800;
body{
    color: $color1;
    padding: 0;
    margin: 0;
    font-family: sans-serif;
    line-height: 23px;
}

.container{
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    padding-right: 15px;
    padding-left:15px;
}

.section-padding{
    padding-top: 25px;
    padding-bottom: 25px;
    @media (min-width:768px){
        padding-top: 80px;
        padding-bottom: 80px;
    }
}
img{
    max-width: 100%;
}
.clearfix{
    &::after,&::before{
        content: "";
        display: table;
        clear: both;
    }
}
.pull-right{
    float: right;
}
.pull-left{
    float: left;
}
.simple-title {
    font-size: 17px;
    margin: 0 0 10px;
}
.main-menu .ant-anchor{
    padding: 0;
    margin: 0;
    list-style-type: none;
    list-style-position: inside;
    display: flex;
    .menu-item{
        margin-left:25px ;
        .ant-anchor-link-title{
            text-decoration: none !important;
            color:$color2;
            padding: 25px 0;
            font-size: 17px;
            display: block;
            &.ant-anchor-link-title-active{
                color: $yellow;
            }
        }
    }
}
header.header {
    position: fixed;
    z-index: 1;
    left: 0;
    right: 0;
    top: 0;
    background: #fff;
}
.padding-top-75{
    padding-top: 75px;
}
.divider{
    margin: 0;
    border-top: 1px solid #ccc;
    border-bottom: 0 none;
    border-left: 0 none;
    border-right: 0 none;
}
p{
    margin-top: 0;
}
.about-section {
    .sec-1 {
        display: flex;
        justify-content: center;
        align-items: center;
        @media (max-width:767px){
            display: block;
            text-align: center;
        }
    }
    .profile-image{
        border-radius: 50%;
        width: 90px;
        height: 90px;
        margin-bottom: 20px;
        @media (min-width:768px){
            width: 200px;
            height: 200px;
            margin-bottom:0 ;
        }
        @media (min-width:992px){
            width: 300px;
            height: 300px;
        }
    }
    .profile-info{
        @media (min-width:768px){
            margin-left: 50px;
        }
    }
}
.profile-name {
    font-size: 25px;
    margin: 15px 0;
    @media (min-width:768px){
        font-size: 40px;
        margin: 25px 0;
    }
    color: $yellow;
}
.brand{
    color: $yellow;
}
.profile-job {
    font-size: 20px;
    margin: 10px 0;
    @media (min-width:768px){
        font-size: 30px;
        margin: 25px 0;
    }
    color: $color0;
 
}
.download-link {
    text-decoration: none !important;
    color: #fff;
    background: #ff9800;
    padding: 5px 20px;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 2px;
    display: inline-block;
    margin-top: 10px;
    @media (min-width:768px){
        margin-top: 15px;
        padding: 20px;
        font-size: 20px;
    }
}
.about-3-block {
    .section-title{
        margin-bottom: 35px;
    }
    display: flex;
    text-align: center;
    @media screen and (max-width:767px) {
        display: block;
    }
    &> div{
        flex: 1 1 33%;
        padding: 0 30px;
    }
    strong{
        color: $color2;
        margin-bottom: 10px;
        display: block;
        font-size: 15px;
    }
    .simple-title{
        margin-top: 35px;
        margin-bottom: 5px;
    }
}
.section-title{
    color: $yellow;
    font-size: 25px;
    margin: 15px 0;
    @media screen and (min-width:768px) {
        margin: 30px 0;
    }
}
.skill-icon {
    display: inline-block;
    width: 25px;
    max-height: 25px;
    vertical-align: middle;
    object-fit: contain;
    margin-right: 15px;
}
.skills-holder {
    max-width: 420px;
    margin: auto;
    padding: 0 15px;
}
.skill-item {
    display: flex;
    align-items: center;
    text-align: left;
    width: 100%;
    justify-content: space-between;
}
.skill {
    display: flex;
    align-items: center;
    font-size: 20px;
    flex: 0 0 150px;
    h5 {
        margin: 15px 0;
        @media screen and (min-width:768px) {
            margin: 20px 0;
        }
    }
}

.progressbar {
    height: 15px;
    background: #eee;
    flex: 1 1 260px;
    direction: ltr;
    .progressbar-fill {
        background: $yellow;
        height: 15px;
        transition: all .1s linear;
    }
}
.text-center{
    text-align: center;
}
.inline-block{
    display: inline-block;
}
.min-h-75{
    min-height: 45px;
    @media screen and (min-width:768px) {
        min-height: 75px;
    }
}
.services-holder{
    display: flex;
    justify-content: space-between;
    .service-item {
        flex: 0 0 25%;
    }
    @media screen and (max-width:767px) {
        display: block;
    }
}
.service-icon {
    width: 70px;
    height: 60px;
    object-fit: contain;
    margin: 10px 0 10px;
    @media screen and (min-width:768px) {
        width: 100px;
        height: 100px;
        margin: 35px 0 20px;
    }
}
.work-items-holder {
    margin-left: -20px;
    margin-right: -20px;
}
.work-item {
    width: 50%;
    float: left;
    padding: 20px;
    box-sizing: border-box; 
    position: relative; 
    opacity: 0;
    &:nth-child(odd){
        left:-30px;
        transition: all .5s ease .4s;
    }
    &:nth-child(even){
        left:30px;
        transition: all .5s ease .8s;
    }
    &.visible{
        left: 0 !important;
        opacity: 1;
    }
    @media screen and (max-width:767px) {
        width: 100%;
        float: none;
    }
}
.work-item-link{
    position: relative;
    display: block;
}
.work-item-text {
    position: absolute;
    background: $yellow;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    opacity: 0;
    transition: all .2s linear;
    a:hover &{
        opacity: 1;
    }
    .simple-title {
        color: #fff;
        font-size: 25px;
        letter-spacing: 1px;
        margin-bottom: 15px;
        position: relative;
        left:-15px;
        opacity: 0;
        transition: all 0.3s linear 0.1s;
        a:hover &{
            opacity: 1;
            left: 0;
        }
    }
    .workItem-description{
        color: #fff;
        margin-bottom: 0;
        font-size: 18px;
        left:15px;
        opacity: 0;
        position: relative;
        transition: all 0.3s linear 0.1s;
        a:hover &{
            opacity: 1;
            left: 0;
        }
    }
}
img.work-thumbnail {
    display: block;
}
footer.footer.text-center {
    padding: 25px 0;
    color:$color2;
}
.max-width-700{
    max-width: 700px;
    margin: auto;
}
.contact-row {
    width: 250px;
    margin: 5px auto;
    display: flex;
    justify-content: space-between;
    a{
        color: $yellow;
        text-decoration: none;
    }
}
.social-link-item {
    margin: 20px 10px;
    display: block;
    color: $color2;
    font-size: 30px;
}
@media screen and (max-width:767px) {
    .ant-anchor-wrapper.main-menu {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: $yellow;
        transform: rotateY(90deg);
        opacity: 0;
    }
}
.portfolio-section{
    overflow: hidden;
}